@import "../../../../assets/styles/app";

.tableMobile {
  @include onlyMobile(flex);

  .left, .right {
    width: 50%;
  }

  .left {
    border-right: 1px solid $color_lines;

    .header {
      display: flex;
      align-items: center;
      padding-left: 12px;
      height: 40px;
      border-bottom: 1px solid $color_lines;

      p {
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: $color_body_50;
      }

    }

    .body {

      .row {
        height: 56px;
        display: flex;
        align-items: center;
        padding-left: 12px;

        &:not(:last-child) {
          border-bottom: 1px solid $color_lines;
        }

        img {
          display: block;
          width: 32px;
          height: 32px;
        }

        p {
          margin-left: 8px;
          font-weight: 400;
          font-size: 12px;
          line-height: 140%;
          color: $color_main_100;
        }
      }
    }

  }

  .right {
    overflow-x: auto;

    .rightInner {
      width: 485px;

      .header {
        height: 40px;
        display: flex;
        border-bottom: 1px solid $color_lines;

        p {
          @include centerRow;
          font-weight: 400;
          font-size: 12px;
          line-height: 140%;
          color: $color_body_50;

          &:nth-child(1) {
            width: 86px;
          }

          &:nth-child(2) {
            width: 142px;
          }

          &:nth-child(3) {
            width: 122px;
          }

          &:nth-child(4) {
            width: 132px;
          }
        }

      }

      .body {

        .row {
          height: 56px;
          display: flex;

          &:not(:last-child) {
            border-bottom: 1px solid $color_lines;
          }

          & > div {
            @include centerRow;

            &:not(:last-child) {
              position: relative;

              &::after {
                position: absolute;
                content: "";
                display: block;
                top: 50%;
                transform: translateY(-50%);
                right: 0;
                width: 1px;
                height: 20px;
                background-color: $color_lines;
              }
            }

            &:nth-child(1) {
              width: 86px;
            }

            &:nth-child(2) {
              width: 142px;
            }

            &:nth-child(3) {
              width: 122px;
            }

            &:nth-child(4) {
              width: 132px;
            }
          }

          .type {

            p {
              position: relative;
              background: rgba(173, 231, 84, 0.08);
              border: 1px solid rgba(173, 231, 84, 0.08);
              padding: 2px 4px 2px 16px;
              font-weight: 400;
              font-size: 12px;
              line-height: 140%;
              text-transform: uppercase;
              color: $color_green;

              &::before {
                content: "";
                display: block;
                position: absolute;
                left: 4px;
                top: 50%;
                transform: translateY(-50%);
                width: 6px;
                height: 6px;
                background: $color_green;
              }
            }

            &_sell {
              p {
                border: 1px solid rgba(255, 84, 0, 0.08);
                background: rgba(255, 84, 0, 0.08);
                color: $color_red;

                &::before {
                  background: $color_red;
                }
              }
            }

          }

          .price {

            p {
              margin-left: 4px;
              font-weight: 400;
              font-size: 12px;
              line-height: 140%;
              color: $color_main_100;

              span {
                color: $color_body_50;
              }
            }
          }

          .time {
            p {
              padding: 4px;
              border-radius: 2px;
              background: $color_white_3;
              font-family: var(--font-family);
              font-weight: 400;
              font-size: 12px;
              line-height: 140%;
              color: $color_body_50;

              &:not(:first-child) {
                margin-left: 3px;
              }
            }
          }

          .transaction {

            p {
              font-weight: 400;
              font-size: 12px;
              line-height: 140%;
              text-decoration: underline;
              text-decoration-skip-ink: none;
              text-align: center;
              color: $color_body_50;
            }
          }

        }
      }

    }
  }
}